import { lazy } from 'react';

export const isPlain = (w) =>
  !w ||
  typeof w === 'function' ||
  typeof w === 'number' ||
  typeof w === 'boolean' ||
  typeof w === 'string';

export const createModuleImporter = (importer) => ({ module, component }) =>
  lazy(async () => ({
    default: (await importer(module))[component]
  }));

export const mergeDescriptors = (first, ...rest) =>
  rest.reduce(
    (acc, { props, ...rest }) => ({
      ...acc,
      ...rest,
      props: { ...acc.props, ...(props || {}) }
    }),
    first
  );

// traverses an entity using a test function - if the test passes, exit with the then,
// if the test fails and we're at a leaf, exit
// otherwise, keep going and keep track of the path
export const traverse = (what, test, then, path = []) =>
  test(what)
    ? then(what, path)
    : isPlain(what)
    ? what
    : Array.isArray(what)
    ? what.map((w, i) => traverse(w, test, then, [...path, i]))
    : Object.entries(what).reduce(
        (acc, [k, v]) => ({
          ...acc,
          [k]: traverse(v, test, then, [...path, k])
        }),
        {}
      );

// Component Cache - TO DO: this is global and ugly - we should fix it
const cache = {};

export const resolveComponent = (resolver, descriptor, cacheKey, fallback) => {
  const k = descriptor[cacheKey] || fallback;
  return cache[k] || (cache[k] = resolver(descriptor));
};
