import { createElement } from 'react';
import { createRefParser, createVarParser } from './parsers';
import { mergeDescriptors, resolveComponent } from './utils';

export const createDynamicRenderer = ({
  ast,
  root = 'MAIN',
  render,
  children = ({ render }) => render(),
  //   Component,
  resolver,
  cacheKey = 'component',
  refKey,
  varPattern,
  merge = mergeDescriptors
}) => {
  const fn = (originalDescriptorOrId, extra = {}, path = [root]) => {
    const key = path.join('//');
    const resolved =
      typeof originalDescriptorOrId === 'string'
        ? ast[originalDescriptorOrId]
        : originalDescriptorOrId;

    if (!resolved)
      throw new Error('Failed to resolved component found at path ', key);

    const parsers = [
      createVarParser(varPattern),
      createRefParser(fn, refKey, path)
    ];

    const next = merge(resolved, extra);

    const props = {
      ast,
      descriptor: next,
      key,
      path,
      render: (innerProps = {}, descriptor = next) => {
        const final = parsers.reduce((acc, p) => p(acc, ast, path), {
          ...descriptor,
          props: { key, ...descriptor.props, ...innerProps },
          key
        });

        return createElement(
          resolveComponent(resolver, final, cacheKey, key),
          final.props,
          final.children
        );
      }
    };

    // return Component
    //   ? createElement(Component, props)
    //   : (render || children)(props);
    return (render || children)(props);
  };

  return fn(root);
};
