import { traverse, isPlain } from './utils';

// parsers traverse descriptor objects and modify them

// a ref parser mixes in a $ref or creates an inline component
export const createRefParser = (dynamicCreator, refKey = '$ref', path) => (
  descriptor
) =>
  traverse(
    descriptor,
    (v) => isPlain(v) || v[refKey],
    (r, path) => {
      if (isPlain(r)) return r;
      const { ref, rest } = Object.entries(r).reduce((acc, [k, v]) => {
        const isKey = k === refKey;
        return {
          ref: isKey ? v : acc.ref,
          rest: isKey ? acc.rest : { ...acc.rest, [k]: v }
        };
      }, {});
      return dynamicCreator(ref, rest, [
        ...path,
        typeof ref === 'string' ? ref : refKey
      ]);
    },
    path
  );

// varParser allows variables in the AST - defined by __ followed by any characters
export const createVarParser = (varPattern = /__(.*)$/) => (descriptor, ast) =>
  traverse(
    descriptor,
    (v) => isPlain(v) || varPattern.test(v),
    (v) => (varPattern.test(v) ? ast[v] : v)
  );
